@import 'variable';
@import 'reset';

body {
	font-family: $font-stack;
}

header{
	width: 100%;	
	color: $color-menu;
	background-color: $back-menu;
	padding: 10px 0;
	display: flex;

	.content-menu{
		display: flex;
		align-items: center;
		width: $l-device;
		margin: 0 auto;
		@include d(s) {
			width: $s-device;
		}

		span a{
			font-size: 2em;
			font-family: $font-logo;
			color: $color-menu;
			font-weight: bold;
			text-decoration: none;
			
			@include d(s) {
				display: block;
				margin-left:10px;
			}	
		}

		nav {
			width: 600px;
			display: flex;
			@include d(s) {
				width: 250px;
				justify-content: center;
				margin-left: 10px;
			}
			
			.section-menu,.redes-sociais-menu{
					display: flex;
					list-style: none;
					justify-content: space-around;
					align-items: center;
					
					a{
						color: $color-menu;
						text-decoration: none;
					}
				}
			.section-menu{
				width: 300px;
				margin-left: 50px;
				@include d(s) {
					margin: 0;
				}
			}

			.redes-sociais-menu{
				margin-left: 100px;
				width: 100px;
				@include d(s) {
					display: none;
				}
			}
		}

		.search-menu{
			margin-left: 300px;
			@include d(s) {
				margin: 0 auto;
			}
		}
	

	}
}

//Content menu
.content-noticias{
	width: $l-device;
	margin: 10px auto;

	.card-post-principal{
		width: $l-device - ($l-device / 100 * 40);
		
	}

	.card-post-principal-overlay{
		position: absolute;
		height: auto;
		width: 100%;
		opacity: 0;
		transition: .3s ease;
		background-color: red;
		box-sizing: border-box;
	}
}

.imagem-content{
	max-width: 400px;

	
	.imagem-content-image{

	}
}

.card-post-link:hover .card-post-principal-overlay {
	opacity: 1;
  }